import React, { useState } from "react";
import data from "../data/question.json";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import Button from "../components/Button";
import * as images from "../assets/images/question";
import Header from "../components/Header";
import { styled } from "styled-components";
import MainContainer from "../components/MainContainer";

const ImageContainer = styled.div`
  /* border: 1px solid #000000; */
  margin: 1.25em 0 0 0;
`;

const QuestionContainer = styled.div`
  /* border: 1px solid #000000; */
  font-weight: bold;
  /* line-height: 1.5625em; */
  line-height: 100%;
  margin: 1.25em 0 0 0;
`;

const Question = () => {
  const count = Object.keys(data).length;
  const questionNumber = Number(useParams().number);
  const currentIdx = questionNumber - 1;
  const { state } = useLocation();
  const location = useLocation();
  const curLocation = location.pathname.replace('/question/', '');
  const [placeSelect, setPlaceSelcet] = useState( curLocation>=5 ? state.placeSelectArr : []);
  const answers = state ? state.answersArr : [];
  
  // const answers = state?.answersArr ??[];

  // ⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️⬇️
  const combinedAnswers = answers.map((answer) => {
    const questionNumber = answer.QNum;
    const answerIndex = answer.AnsIdx;
    const question = data[questionNumber - 1].question;
    let answerText = "";

    if (questionNumber === 1) {
      answerText = answerIndex === 0 ? "E" : "I";
    } else if (questionNumber === 2) {
      answerText = answerIndex === 0 ? "S" : "N";
    } else if (questionNumber === 3) {
      answerText = answerIndex === 0 ? "T" : "F";
    } else if (questionNumber === 4) {
      answerText = answerIndex === 0 ? "J" : "P";
    }

    return {
      questionNumber,
      question,
      answerIndex,
      answerText,
    };
  });
  

  const answerTextCombined = combinedAnswers
    .filter((answer) => answer.questionNumber <= 4)
    .map((answer) => answer.answerText)
    .join("");

  // console.log(answerTextCombined);
  // ⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️⬆️

  const navigate = useNavigate();

  const AnswerList = ({ answer, index }) => {
    const updateAnswers = () => {
      const nextQuestionNumber = questionNumber + 1;
      const existIndex = answers.findIndex(
        (answer) => answer.QNum === questionNumber
      );
      let newAnswers = [];
      if (existIndex !== -1) {
        newAnswers = [...answers];
        newAnswers[existIndex] = { ...newAnswers[existIndex], AnsIdx: index };
      } else {
        newAnswers = [...answers, { QNum: questionNumber, AnsIdx: index }];
      }

      if (questionNumber === Number(count)) {
        setPlaceSelcet([...placeSelect, index]);
        navigate(`/result`, {
          state: {
            answersArr: newAnswers,
            answerTextCombined: answerTextCombined,
            placeSelectArr: placeSelect,
          },
        });
      } else {
        if (questionNumber >= 5) {
          setPlaceSelcet([...placeSelect, index]);
          navigate(`/question/${nextQuestionNumber}`, {
            state: {
              answersArr: newAnswers,
              answerTextCombined: answerTextCombined,
              placeSelectArr: placeSelect,
            },
          });
        } else {
          navigate(`/question/${nextQuestionNumber}`, {
            state: {
              answersArr: newAnswers,
              answerTextCombined: answerTextCombined,
            },
          });
        }
        
      }
    };

    return (
      <Button
        type={"basic"}
        onClick={updateAnswers}
        height={"58px"}
        style={{ margin: "1em 0 0 0" }}
      >
        {answer}
      </Button>
    );
  };

  const QuestionImg = (number) => {
    const ShowImage = images[`question_${number}`];
    return <ShowImage />;
  };

  return (
    <MainContainer>
      <Header count={count} current={questionNumber} />
      <ImageContainer>{QuestionImg(questionNumber)}</ImageContainer>
      <QuestionContainer>{data[currentIdx].question}</QuestionContainer>
      {data[currentIdx].answer.map((answer, index) => {
        return <AnswerList key={index} answer={answer} index={index} />;
      })}
    </MainContainer>
  );
};

export default Question;
